@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Raleway", sans-serif;
}

/* Media Queries */
@media (min-width: 1600px) {
  html {
    font-size: 60%;
  }
}

@media (max-width: 1200px) {
  html {
    font-size: 57%;
  }
}

@media (max-width: 1100px) {
  html {
    font-size: 55%;
  }
}

@media (max-width: 900px) {
  html {
    font-size: 52.5%;
  }
}

@media (max-width: 700px) {
  html {
    font-size: 47.5%;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 45%;
  }
}


.navbar__main {
  background-color: #7475f9;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 4rem;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}

.navbar__title {
  font-size: 2.5rem;
  font-weight: bold;
}

.navbar__title a {
  text-decoration: none;
  color: white;
}

.navbar__title span {
  color: #5ce8d3;
}

#menu__toggle {
  opacity: 0;
}

.menu__btn > span.active {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.menu__btn > span.active::before {
  top: 0;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.menu__btn > span.active::after {
  top: 0;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.menu__box {
  visibility: visible;
  left: 0;
}

.hamburger-menu {
  position: relative;
  z-index: 90;
  -webkit-transform: translateX(7.5px);
          transform: translateX(7.5px);
}

.menu__btn {
  width: 25px;
  display: none;
  height: 25px;
  cursor: pointer;
}

.menu__btn > span {
  display: block;
  position: absolute;

  width: 28px;
  height: 2px;

  background-color: #fff;

  transition-duration: 0.25s;
}
.menu__btn > span::before,
.menu__btn > span::after {
  display: block;
  position: absolute;

  width: 100%;
  height: 2px;

  background-color: #fff;

  transition-duration: 0.25s;
}
.menu__btn > span::before {
  content: "";
  top: -8px;
}
.menu__btn > span::after {
  content: "";
  top: 8px;
}

/* Media */
@media (max-width: 800px) {
  .navbar__nav {
    display: none;
  }
  .menu__btn {
    display: block;
  }
}

.nav__ul {
  display: flex;
  list-style: none;
  align-items: center;
}

.nav__list--li a {
  display: inline-block;
  padding: 1.5rem;
  font-size: 1.3rem;
  margin-right: 1.5rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  color: #ffffff;
  letter-spacing: 3px;
  border-radius: 16px;
}

.nav__list--li a:hover {
  color: #5ce8d3;
}

.nav__list--login a {
  background: linear-gradient(to right bottom, #37c6f2, #47d2e3, #5ce8d3);
  padding: 1.5rem 4rem;
}

.nav__list--login a:hover {
  color: white;
  background: linear-gradient(to right bottom, #36bae2, #3fbccc, #53d3c0);
}

.footer__container {
  padding: 3rem;
  margin-top: 5rem;
  /* height: 37.5rem; */
