  color: white;
}

.footer__title span {
  color: #5ce8d3;
}

.footer__ul {
  margin: 4rem;
}

.footer__ul a {
  margin-right: 5rem;
  padding: 1rem 2rem;
  transition: all 0.4s;
  border-bottom: 2px solid #50c9b7;
  border-radius: 0;
}

.footer__ul a:hover {
  color: white;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.footer__credit {
  text-transform: uppercase;
